.wrapper {
  width: 30vw;
  margin-bottom: 20px;

  .form {
    .text_field {
      width: 100%;
      margin-right: 10px;
    }

    .button_wrapper {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      margin-top: 20px;
      
      .submit_button,
      .cancel_button {
        width: 100%;
        height: 40px;
        font-weight: bold;
        font-size: 15px;
        border: none;
        border-radius: 5px;
        
        &:hover {
          cursor: pointer;
        }
      }
      
      .submit_button {
        margin-bottom: 10px;
        background-color: #ffd5c9;
        color: #fff;
      }
      
      .cancel-button {
        color: #282828;
      }
    }
    }
  }

